<template>
  <div>
    <el-button v-if="isAdding" class="button" @click="addToCart">加入购物车</el-button>
    <el-button v-else class="button" @click="removeFromCart(book._id)">从购物车移除</el-button>
  </div>
</template>

<script>
export default {
  props: ['book'],
  computed: {
    isAdding() {
      let isAdding = true;
      this.cart.map(book => {
        if (book._id === this.book._id) {
          isAdding = false;
        }
      });
      return isAdding;
    },
    cart() {
      return this.$store.state.cart;
    }
  },
  methods: {
    addToCart() {
      this.$store.commit('ADD_TO_CART', {
        book: this.book,
        
      })
      this.$message('添加成功');
    },
    removeFromCart(bookId) {
      this.$store.commit('REMOVE_FROM_CART', {
        bookId,
      })
      this.$message('取消成功');
    }
  }
}
</script>
<style scoped>
    .el-button{
      padding: 6px;
      background-color: #ff5722;
      border-radius: 2px;
      color: #fff;
      border: 0;
    }
    .el-button:hover{
       background-color: #ff5722;
    }
</style>>


